<template>
    <yd-layout>
        <div class="invoiceTop">
            <yd-flexbox>
                <yd-flexbox-item v-text="jsonObj.title"></yd-flexbox-item>
                <div v-text="jsonObj.infoTime"></div>
            </yd-flexbox>
        </div>
        <div class="mainContentFluid mainInvoice">
            <yd-flexbox>
                <div class="header">抬头名称：</div>
                <yd-flexbox-item v-text="jsonObj.headerName"></yd-flexbox-item>
            </yd-flexbox>
            <yd-flexbox>
                <div class="headerTwo">税 号：</div>
                <yd-flexbox-item v-text="jsonObj.taxId"></yd-flexbox-item>
            </yd-flexbox>
            <yd-flexbox>
                <div class="headerThree">单位地址：</div>
                <yd-flexbox-item v-text="jsonObj.address"></yd-flexbox-item>
            </yd-flexbox>
            <yd-flexbox>
                <div class="header">电话号码：</div>
                <yd-flexbox-item v-text="jsonObj.telephone"></yd-flexbox-item>
            </yd-flexbox>
            <yd-flexbox>
                <div class="header">开户银行：</div>
                <yd-flexbox-item v-text="jsonObj.bank"></yd-flexbox-item>
            </yd-flexbox>
            <yd-flexbox>
                <div class="header">银行账号：</div>
                <yd-flexbox-item v-text="jsonObj.account"></yd-flexbox-item>
            </yd-flexbox>
        </div>
        <div class="mainContentFluid mainCode">
            <yd-flexbox>
                <div class="header">开票代码：</div>
                <yd-flexbox-item v-text="jsonObj.code"></yd-flexbox-item>
            </yd-flexbox>
            <div class="codePhoto">
                <div class="erwei">
                    <img :src="'https://mobileoa.unionpay.com' + jsonObj.codeImage">
                </div>
                <div class="codeText">商家扫二维码，快速获得抬头信息</div>
            </div>
        </div>
        <div class="mainContentFluid instruction">
            <yd-flexbox>
                <div>开票说明</div>
            </yd-flexbox>
            <div class="fluidWidth">
                <div class="onePart" v-html="instruction">
                </div>
            </div>
        </div>
    </yd-layout>
</template>
<script>
import $ from '../../jquery-vendor';
import { mapGetters } from 'vuex';
export default {
    name: 'invoice',
    data() {
        return {
            instruction: '',
            jsonObj: {},
            num: 0
        }
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    mounted() {
        document.setTitle('开发票');
        this.initXml();
    },
    methods: {
        initXml() {
            var vm = this;
            vm.xml(vm.conf.wcm + '/app/kfp2/index.xml', {}, function (res) {
                vm.instruction = $(res).find('instruction').text();
                let json = $.parseJSON($(res).find('json').text()).jsonData;
                console.log(vm.instruction);
                for (let i = 0; i < json.length; i++) {
                    if (json[i].headerName.indexOf(vm.getUserInfo.deptname) != -1) {
                        vm.jsonObj = json[i];
                    } else {
                        vm.num++;
                    }
                }
                if (vm.num == 36) {
                    vm.jsonObj = json[0];
                }
                console.log(vm.num);
            });
        }
    }
}
</script>
<style scoped>
.invoiceTop {
    height: 0.68rem;
    width: 100%;
    line-height: 0.68rem;
    font-size: 0.24rem;
    color: #999;
    padding: 0 0.25rem;
}

.time {
    padding-left: 0.21rem;
}

.headerTwo {
    word-spacing: 0.46rem;
    color: #999;
}

.headerThree {
    position: absolute;
    line-height: 0.6rem;
    color: #999;
    top: 0;
}

.codePhoto img {
    width: 4.8rem;
    height: 4.76rem;
    margin: 0 auto;
}

.codeText {
    text-align: center;
    color: #35a4ff;
    font-size: 0.28rem;
    margin-top: 0.2rem;
    padding-bottom: 0.2rem;
}

.instruction .yd-flexbox {
    border-bottom: 1px solid #f1f1f1;
    font-size: 0.28rem;
    color: #333;
    line-height: 0.8rem;
    padding-left: 0.33rem;
}

.onePart {
    font-size: 0.26rem;
    padding-bottom: 0.27rem;
}

.blackWord {
    color: #333;
    font-weight: bold;
}

.redWord {
    color: #f00;
    font-weight: bold;
}

.erwei {
    text-align: center;
}
</style>


